import React, { Component } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';

class Forms extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      direction: '',
      hora: '',
      fecha: '',
      sector: '',
      type: '',
      description: '',
      phone: ''

    };
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }

  valida = (campo) => {
    return (valor) => {
      this.setState({
        [campo]: valor.target.value,
      })
    }
  }

  render() {
    console.log(this.state)
    return (
      <div className="animated fadeIn">

        <Row>
          <Col xs="12" md="12">
            <Card>
              <CardHeader>
                <strong>Service</strong>
              </CardHeader>
              <CardBody>
                <Form action="" method="post" encType="multipart/form-data" className="form-horizontal">
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="text-input">Direccion</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="text"  id="text-input" name="text-input" placeholder="Text" onChange={this.valida('direction')} />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="phone-input">telefono de contacto</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="number" id="phone-input" name="phone-input" placeholder="Enter phone"  onChange={this.valida('phone')} autoComplete="phone"/>
                    </Col>
                  </FormGroup>

                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="date-input">Fecha del Servicio</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="date" id="date-input" name="date-input" placeholder="date"  onChange={this.valida('fecha')} />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="hour-input">Hora del servicio</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="text" id="hour-input" name="hour-input" placeholder="Enter hour" autoComplete="hour"  onChange={this.valida('hora')}/>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="textarea-input">Anotaciones a tener en cuenta</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="textarea" name="textarea-input" id="textarea-input" rows="9"
                             placeholder="Content..."  onChange={this.valida('description')} />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="select">Sector</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="select" name="select" id="select"  onChange={this.valida('sector')}>
                        <option value="0">Seleccione un sector</option>
                        <option value="1">Sur</option>
                        <option value="2">Norte</option>
                        <option value="3">Este</option>
                        <option value="4">Oeste</option>
                      </Input>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="type">Tipo de servicio</Label>
                    </Col>
                    <Col xs="12" md="9">
                      <Input type="select" name="type" id="type"  onChange={this.valida('type')}>
                        <option value="0">Seleccione un tipo</option>
                        <option value="1">Sur</option>
                        <option value="2">Norte</option>
                        <option value="3">Este</option>
                        <option value="4">Oeste</option>
                      </Input>
                    </Col>
                  </FormGroup>
                </Form>
              </CardBody>
              <CardFooter>
                <Button type="submit" size="sm" color="primary" onClick={()=>console.log('holi')}><i className="fa fa-dot-circle-o"></i> Submit</Button>
                <Button type="reset" size="sm" color="danger"><i className="fa fa-ban"></i> Reset</Button>
              </CardFooter>
            </Card>
          </Col>
        </Row>


      </div>
    );
  }
}

export default Forms;
